import { useEffect } from 'react'
import { useTranslation } from "react-i18next";



const Chatbot = () => {
  const { i18n, t } = useTranslation();

  useEffect(() => {
    const script = document.createElement('script');

    script.src = "https://gradio.s3-us-west-2.amazonaws.com/3.40.0/gradio.js";
    script.async = true;

    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script);
    }
  }, []);
   return (
    <div className="chatbot">
    <section className='section contact center' id='chatbot'>
      <h2 className='section__title'>DaveAI</h2>
      <gradio-app src="https://sharmad-daveai.hf.space" eager="true" autoscroll="true" initial_height="200px" container="false" theme_mode="light"/>

      

    </section>
    </div>
  )
   }

export default Chatbot
